@import "../../app.scss";

.parallax {
  height: 100%;
  width: 100%;
  position: relative;
  display: flex;
  align-items: center;
  justify-content: center;
  overflow: hidden;

  h1 {
    font-size: 60px;
    @include mobile {
      font-size: 45px;
    }
  }
  .mountains {
    background-image: url("../../../public/mountains.png");
    background-size: cover;
    background-position: bottom;
    width: 100%;
    height: 100%;
    position: absolute;
    z-index: 3;
    @include mobile {
      background-size: contain;
      background-repeat: no-repeat;
    }
  }
  .planets {
    // background-image: url("../../../public/planets.png");
    background-size: cover;
    background-position: bottom;
    width: 100%;
    height: 100%;
    position: absolute;
    z-index: 2;
    @include mobile {
      background-size: contain;
      background-repeat: no-repeat;
    }
  }
  .stars {
    background-image: url("../../../public/stars.png");
    background-size: cover;
    background-position: bottom;
    width: 100%;
    height: 100%;
    position: absolute;
    z-index: 1;
    @include mobile {
      background-size: cover;
      background-repeat: no-repeat;
    }
  }
}
